/* General & Header */

.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

#boxWrapper {
  margin: 10px 50px;
  display: block;
}

#headerBox {
  max-width: 400px;
  margin: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.sock--left {
  -webkit-animation: sock-left-spin infinite 20s linear;
          animation: sock-left-spin infinite 20s linear;
  height: 40px;
  padding: 20px;
}

.sock--right {
  -webkit-animation: sock-right-spin infinite 20s linear;
          animation: sock-right-spin infinite 20s linear;
  height: 40px;
  padding: 20px;
}

i.material-icons {
  font-size: 18px;
  cursor: pointer;
}

input {
  font-size: 1.2em;
  border: none;
  text-align: center;
  background-color: inherit;
}

.App-header {
  background-color: #ffffff;
  height: 150px;
  padding: 20px;
  color: black;
  height: auto;
}

.App-title {
  font-size: 2em;
}

button, input[type="file"] button {
  background-color: white;
  color: black;
  margin: 5px 0px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes sock-left-spin {
  from { -webkit-transform: rotate(0deg) scaleX(-1); transform: rotate(0deg) scaleX(-1); }
  to { -webkit-transform: rotate(360deg) scaleX(-1); transform: rotate(360deg) scaleX(-1); }
}

@keyframes sock-left-spin {
  from { -webkit-transform: rotate(0deg) scaleX(-1); transform: rotate(0deg) scaleX(-1); }
  to { -webkit-transform: rotate(360deg) scaleX(-1); transform: rotate(360deg) scaleX(-1); }
}

@-webkit-keyframes sock-right-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(-360deg); transform: rotate(-360deg); }
}

@keyframes sock-right-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(-360deg); transform: rotate(-360deg); }
}

/* Box */

#dataSave {
  float: left;
}

#dataSave button {
  float: left;
}

.list {
  background-color: rgba(0, 0, 0, .05);
  margin: 5px;
  min-width: 50px;
  min-height: 20px;
  border: 2px solid black;
  border-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
}

#legend {
  list-style-type: none;
  width: 350px;
  text-align: right;
  float: right;
}

#mobile {
  display: block;
  float: left;
  text-align: left;
  clear: both;
}

#website {
  float: right;
  clear: both;
  text-decoration: none;
  color: white;
}

/* Item */

.item {
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding: 5px;
  min-width: 20%;
  min-height: 20px;
}

/* File Selector */

#fileSelector {
  float: left;
}

#fileSelector button {
  float: left;
}

#fileSelector input {
  font-size: 12px;
}

/* Responsive */

@media(min-width: 600px){
  #mobile {
    display: none;
  }
}



